/* Base styles remain the same as your original CSS */
.home {
  overflow: hidden;
  position: relative;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  text-align: left;
  padding: 0px 40px 40px 40px;
  box-sizing: border-box;
}

.welcome-message {
  z-index: 1;
  margin-top: 0px;
  font-size: 45px;
  margin-bottom: 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  text-align: center;
}

.h2-container {
  box-sizing: border-box;
  padding: 40px 140px;
  height: min-content;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  min-width: min-content;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 12px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
}

#background-video {
  height: 100%;
  width: 100%;
  object-fit: cover;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 0;
  margin: 0;
  z-index: 0;
  filter: brightness(1);
}
.home button {
  background-color: white;
  width: 200px;
  text-shadow: none !important;
  height: 50px;
  color: black;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  font-size: larger;
  cursor: pointer;
  transition: background-color 0.1s ease-in-out, width 0.1s ease-in-out;
  padding: 5px;
  position: relative;
}

.home button span {
  position: relative;
  display: inline-block;
}

.home button span::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: -2px;
  left: 50%;
  background-color: black;
  transition: width 0.3s ease-in-out;
  transform: translateX(-50%);
}

.home button:hover span::after {
  width: 100%;
}

.home button:hover {
  width: 220px;
}

.home button:active {
  width: 250px;
}
.home button * {
  text-shadow: none !important; 
}
/* Media queries for smaller screens */
@media screen and (max-width: 768px) {
  .home {
    margin-top: auto;
    padding: 0px 20px 20px 20px;
  }
  
  .welcome-message {
    font-size: 35px;
  }
  
  .h2-container {
    padding: 30px 70px;
  }
  
  .home button {
    width: 180px;
    height: 45px;
    font-size: medium;
  }
  
  .home button:hover {
    width: 200px;
  }
  
  .home button:active {
    width: 220px;
  }
}

@media screen and (max-width: 480px) {
  .home {
    margin-top: -100px;
    padding: 0px 15px 15px 15px;
  }
  
  .welcome-message {
    font-size: 28px;
  }
  
  .h2-container {
    padding: 20px 30px;
  }
  
  .home button {
    width: 160px;
    height: 40px;
    font-size: small;
  }
  
  .home button:hover {
    width: 170px;
  }
  
  .home button:active {
    width: 180px;
  }
}